<template>
<div id="counter">
  <h1>Quantity</h1>
    <div class="quantity-toggle">
      <button @click="decrement()">&mdash;</button>
      <input type="text" :value="quantity" readonly>
      <button @click="increment()">&#xff0b;</button>
    </div>
</div>
</template>

<script>
import catagories from '@/components/catagories.vue'
import promotionCarousel from '@/components/promotionCarousel.vue'


export default {
  name: 'Home',
  components: {
    catagories,
    promotionCarousel
  },
  data () {
    return {
        quantity: 1
    }
  },
  methods: {
    increment () {
      this.quantity++
    },
    decrement () {
      if(this.quantity === 1) {
        alert('Negative quantity not allowed')
      } else {
        this.quantity--
      }
    }
  }
}
</script>

