import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Contact from '../views/Contact.vue'
import Account from '../views/Account.vue'
import CovidUpdate from '../views/CovidUpdate.vue'
import FAQ from '../views/FAQ.vue'
import Returns from '../views/Returns.vue'
import Terms from '../views/Terms.vue'
import PrivacyPolicy from '../views/PrivacyPolicy.vue'
import ElectricGuitars from '../views/ElectricGuitars.vue'
import BassGuitars from '../views/BassGuitars.vue'
import AcousticGuitars from '../views/AcousticGuitars.vue'
import Amps from '../views/Amps.vue'
import Pedals from '../views/Pedals.vue'
import Accessories from '../views/Accessories.vue'
import SignUp from '../views/SignUp.vue'
import Product from '../views/Product.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {title: 'Tones Guitar Shop'}
  },
  {
    path: '/Contact',
    name: 'Contact',
    component: Contact,
    meta: {title: 'Tones - Contact Us'}
  },
  {
    path: '/Account',
    name: 'Account',
    component: Account,
    meta: {title: 'Tones - Account'}
  },
  {
    path: '/CovidUpdate',
    name: 'CovidUpdate',
    component: CovidUpdate,
    meta: {title: 'Tones - Covid Update'}
  },
  {
    path: '/FAQ',
    name: 'FAQ',
    component: FAQ,
    meta: {title: 'Tones - FAQ'}
  },
  {
    path: '/Returns',
    name: 'Returns',
    component: Returns,
    meta: {title: 'Tones - Returns'}
  },
  {
    path: '/Terms',
    name: 'Terms',
    component: Terms,
    meta: {title: 'Tones - Terms'}
  },
  {
    path: '/PrivacyPolicy',
    name: 'PrivacyPolicy',
    component: PrivacyPolicy,
    meta: {title: 'Tones - Privacy Policy'}
  },
  {
    path: '/ElectricGuitars',
    name: 'ElectricGuitars',
    component: ElectricGuitars,
    meta: {title: 'Tones - Electric Guitars'}
  },
  {
    path: '/BassGuitars',
    name: 'BassGuitars',
    component: BassGuitars,
    meta: {title: 'Tones - Bass Guitars'}
  },
  {
    path: '/AcousticGuitars',
    name: 'AcousticGuitars',
    component: AcousticGuitars,
    meta: {title: 'Tones - Acoustic Guitars'}
  },
  {
    path: '/Amps',
    name: 'Amps',
    component: Amps,
    meta: {title: 'Tones - Amps'}
  },
  {
    path: '/Pedals',
    name: 'Pedals',
    component: Pedals,
    meta: {title: 'Tones - Pedals'}
  },
  {
    path: '/Accessories',
    name: 'Accessories',
    component: Accessories,
    meta: {title: 'Tones - Acessories'}
  },
  {
    path: '/SignUp',
    name: 'SignUp',
    component: SignUp,
    meta: {title: 'Tones - Sign Up'}
  },
  {
    path: '/Product/:itemId',
    name: 'Product',
    component: Product,
    props: true,
    meta: {title: 'Tones - Products'}
  },
]

const router = new VueRouter({
  routes,
  
  
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 };
  }
})

router.beforeEach((toRoute, fromRoute, next) => {
  window.document.title = toRoute.meta && toRoute.meta.title ? toRoute.meta.title : 'Home';

  next();
})

export default router
