<template>
<div id="product-page">
<div class="product-images"><img :src="product.image"></div>
<div class="product-info">
  <h1>{{product.brand}} {{product.model}} - {{product.color}}</h1>
<h2 class="price">${{product.price}}</h2>
<div class="review">
  <li v-for="(star, index) in stars" :class="rating > index ? 'active' : ''" @click="ratingStar(index)"></li>
</div>
<readmore />
  <div class="add-cart">
    <div id="amount"><quantity /></div>
    <button>Add to Cart</button>
  </div>
</div>
  <div class="carousel">
    <itemCarousel />
  </div>
</div>
</template>

<script>
import readmore from '@/components/readmore.vue'
import quantity from '@/components/quantity.vue'
import itemCarousel from '@/components/itemCarousel.vue'

export default {
  components: {
    readmore,
    quantity,
    itemCarousel
  },
  data () {
    var product = this.$route.params
    return {
      rating: 0,
      stars: 5,
      show: false,
        product: {
          itemId: product.itemId,
          brand: product.brand,
          model: product.model,
          color: product.color,
          price: product.price,
          image: product.image,
          description: product.description,
          readMoreActivated: false
        }
    }
  },
  methods: {
      ratingStar (index) {
        this.rating = index + 1;
      },
    }
}
</script>

