        </a>
        <span v-if="readMoreActivated" v-html="description"></span>
    </div>
</template>

<script>
export default {
    name: "readmore",
  data () {
    var product = this.$route.params
    return {
      show: false,
          description: product.description,
          readMoreActivated: false 
    }
  },
  methods: {
    activateReadMore(){
        this.readMoreActivated = true;
    },
}
}
</script>

